









































.viewTable-box {
  /deep/ .el-dialog {
    width: 1100px;
  }
  /deep/ .el-dialog__header {
    padding: 0 80px 0 20px;
    border-bottom: 1px solid #eee;
    background: #4b77be;
    height: 30px;
    line-height: 30px;
  }
  /deep/ .el-dialog__title {
    font-size: 14px;
    color: #ffffff;
  }
  /deep/ .el-dialog__headerbtn {
    top: 6px;
  }
  /deep/ .el-dialog__headerbtn .el-dialog__close {
    color: #ffffff;
  }
  /deep/ .el-dialog__body {
    padding: 0;
    height: 700px;
  }
  /deep/ .el-dialog__footer {
    border-top: 1px solid #e5e5e5;
  }

  .content-box {
    position: relative;
    .cancel-box {
      height: 28px;
      line-height: 28px;
      background: #4b77be;
      position: absolute;
      top: -30px;
      right: 20px;
      z-index: 20;
      color: #ffffff;
      font-size: 14px;
      cursor: pointer;
    }

    .content_box {
      padding: 16px;
    }
  }
}
